@import "../../../style/styles.scss";

.login {
  margin-bottom: 34px;
  height: 100vh;

  &-form {
    padding: 40px !important;
    background-color: $bg-gray;

    &-button {
      width: 100%;
    }

    &-forgot {
      margin-left: 20px;
      color: $blue-color;
    }

    &-register {
      color: $blue-color;
    }
  }

  &-title {
    margin-bottom: 16px;
    font-size: 2rem;
  }
}
